import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Radio, InputNumber, Col, Checkbox } from "antd";

//const { Option } = Select;
const InputGroup = Input.Group;

class PaymentTermCreateForm extends Component {
  state = {
    check_code:false
  }
  
  handleChangeCode = () => {
    const {setFieldsValue} = this.props.form
    this.setState({check_code:!this.state.check_code})
    if(this.state.check_code===false){
      setFieldsValue({
        ["payment_term_code"]: null,
      })
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    //console.log(this.props.paymentTerm);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { paymentTerm } = this.props;
    const { loading } = paymentTerm;
    const error = paymentTerm.errorField ? paymentTerm.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = { 
      display: "flex", 
      width: "100%" 
    }

    const radioButtonStyle = { 
      flex: "1", 
      textAlign: "center" 
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="paymentTerms.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <InputGroup compact>
              <Col span={20}>  
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="paymentTerms.add.code" />}
                  validateStatus={
                    error.payment_term_code || getFieldError("payment_term_code")
                      ? "error"
                      : null
                  }
                  help={
                    error.payment_term_code
                      ? error.payment_term_code
                      : getFieldError("payment_term_code")
                  }
                >
                  {getFieldDecorator("payment_term_code", {
                    //initialValue: data.payment_term_code,
                    rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(
                    <Input
                      disabled={this.state.check_code ? true : false}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="autoGenerateCode" />}
                >
                  <Checkbox
                    style={{  
                      paddingTop:"5px",
                      paddingLeft: "8px",
                    }}
                    checked={this.state.check_code}
                    onChange={this.handleChangeCode}
                  />
                  
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="name" />}
            >
              {getFieldDecorator("description", {
                //initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input />)}
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="isCash" />}
                >
                  {getFieldDecorator("is_cash", {
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(
                    <Radio.Group
                      buttonStyle="solid"
                      style={radioGroupStyle}
                    >
                      <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="yes" /></Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="no" /></Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="paymentTerms.add.default" />}
                >
                  {getFieldDecorator("is_default", {
                    initialValue: 0,
                  })(
                    <Radio.Group //disabled={used ? true : false} 
                      buttonStyle="solid"   
                      style={radioGroupStyle}

                    >
                      <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="yes" /></Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="no" /></Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            


            {/* <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="paymentTerms.add.discount" />}
            >
              {getFieldDecorator("discount", {
                initialValue: 0,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                ]
              })(
                <InputNumber
                  //disabled={used ? true : false}
                  min={0}
                  max={100}
                  formatter={value => `${value}%`}
                  parser={value => value.replace('%', '')}
                />
              )}
            </Form.Item> */}
          {/* </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}> */}
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="paymentTerms.add.minimumPaymentDay" />}
                >
                  {getFieldDecorator("minimum_payment_day", {
                    //initialValue: data.minimum_payment_day,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      //disabled={used ? true : false}
                      style={{ width:"100%" }}
                      precision={0}
                      min={0}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="paymentTerms.add.netPaymentDay" />}
                >
                  {getFieldDecorator("net_payment_day", {
                    //initialValue: data.net_payment_day,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      style={{ width:"100%" }}
                      precision={0}
                      min={0} 
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
            
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedPaymentTermCreateForm = Form.create({ name: "create_payment_term" })(PaymentTermCreateForm);

export default WrappedPaymentTermCreateForm;
