import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PaymentTermCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/payment/paymentTerm/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/payment-terms"

class CreatePaymentTerm extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createPaymentTerm } = this.props;
    createPaymentTerm();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.paymentTerm.error) {
      notification("error", this.props.paymentTerm.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.paymentTerm.success) {
      this.props.history.replace(redirectTo);
    }
  }

  handleSubmit = value => {
    const { storePaymentTerm } = this.props;

    // Uppercasing paymentTerm code
    value.discount = value.discount / 100;
    // console.log(value);
    storePaymentTerm(value);
  };

  render() {

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="paymentTerms.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <PaymentTermCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            paymentTerm={this.props.paymentTerm}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    paymentTerm: state.paymentTerm
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreatePaymentTerm);
